<a-tooltip :title="text"><span class="ellipsis" v-if="type === &quot;length&quot;">{{showText}}</span><span class="ellipsis text-flow-ellipsis-multiple" v-else-if="type === &quot;rows&quot;" :style="typeRows()">{{showText}}</span><div class="ellipsis box" v-else-if="type === &quot;height&quot;" ref="ellipsis" :style="typeHeight()">{{showText}}</div></a-tooltip>